import { environment } from '../../environments/environment';
import * as Mock from 'mockjs';
import MapMock from './map.mock';
import UserMock from './user.mock';
import ArticleMock from './article.mock';
import AdvertMock from './advert.mock';

export interface IMockBase {
  url: string | RegExp;
  method: 'get' | 'post' | 'put' | 'delete';
  result: any;
}

Mock.setup({
  timeout: '500-1000',
});

const routerList: IMockBase[] = [
  // TODO register mock data here
  ...MapMock,
  ...UserMock,
  ...ArticleMock,
  ...AdvertMock,
];

routerList.forEach((item) => {
  const url = environment.BASE_URL + item.url;
  Mock.mock(url, item.method, item.result);
});

export default Mock;
